@mixin my-alert-1 {
  background: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
  p {
    margin-bottom: 0;
  }
}

.my-alert {
  @include my-alert-1();
}

.my-alert-red {
  @extend .my-alert;
  background: #ffe591;
  // display: none;
}
